* {
    margin: 0;
    padding: 0;
}

html {
    width: 100%;
    height: 100%;
}

body {
    background: #000;
    color: #ccc;
    font-size: 12px;
    line-height: 1.5;
    display: block;
    height: 100%;
}

blockquote {
    border-left: .25em solid #393e3f;
    color: #6a737d;
    padding: 0 1em;
}

/*******************************************************/

/* HEADER */

.header_body {
    width: 100%;
    background-color: #1b1b1b;
    margin-bottom: 20px;
}

.header_content {
    padding-top: 8px;
    margin: 0 auto;
    height:38px;
    width: 960px;
}

/*******************************************************/

/* CONTENT CONTAINER */

.content_container {
    //margin: 0 30px 0 30px;
    font: 12px Arial, Verdana, Geneva, sans-serif;
    //font-family: 12px Arial, Verdana, sans-serif, "メイリオ", Meiryo, "Hiragino Sans", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "ＭＳ Ｐゴシック","FFXIV_Lodestone_SSF";
    text-decoration: none;

    margin: 0 auto;
    width: 1000px;
}

.content_container a {
    color: #497cf5;
    text-decoration: none;
}

.content_container a:hover {
    text-decoration: underline;
}

.content_inner_container {
    // Add trasparent repeating background?
    padding: 10px 20px 10px 20px;
    border-radius: 5px;
    border: 1px solid #ccc;
    background: url(/assets//images/bg.jpg) center top no-repeat;
}

/*******************************************************/

/* LATEST POST */

.latest_post {
    width: 646px;
    height: 28px;
    margin-top: 70px;
    margin-right: 290px;
    padding-top: 11px;
    border: 1px solid rgba(76, 115, 137, 0.746);
    background-color: #1b1b1b66;
}

.latest_post span {
    
    padding-left: 10px;
}

.post {
    width: 626px;

    margin-right: 290px;
  

    padding-left: 10px;
    padding-right: 10px;
    padding-top: 20px;
    padding-bottom: 20px;

    border-left: 1px solid rgba(76, 115, 137, 0.746);
    border-bottom: 1px solid rgba(76, 115, 137, 0.746);
    border-right: 1px solid rgba(76, 115, 137, 0.746);

    background-color: #1b1b1b66;
}

/*******************************************************/

/* SIDEBAR */

.sidebar {
    float: right;
    margin-top:82px;
    width: 280px;
    border: 1px solid rgba(76, 115, 137, 0.746);
    border-radius: 5px;
    padding: 10px;
}

.sidebar_inner {
    width: 100%;
    border-radius: 3px;
    border: 1px solid rgba(76, 115, 137, 0.746);
}

.sidebar_inner li {
    list-style: none;
    font-size: 12px;
    padding: 10px;
}